export default {
	data() {
		return {
			title: 'Hello',
			user: {}
		}
	},
	onLoad() {
		let that = this
		uni.getStorage({
		    key: 'user',
		    success: function (res) {
				that.user = JSON.parse(res.data)
		    }
		});
	},
	methods: {
		toLogin: function(){
			if (!this.user.username) {
				uni.navigateTo({
					url:'../../login/login'
				})	
			}
		}
	}
}
